<template>
  <!-- 可移动元素 -->
  <h-portabilityBox
    :openPortability="true"
    :title="'任务编辑'"
    :top="'150px'"
    @closePortability="closePortability"
  >
    <div class="add-task-page">
      <!-- 性质 -->
      <div class="addtask-set-item-box">
        <p class="set-title">性质：</p>
        <p
          class="set-choice"
          v-for="item in typeList"
          :key="item.id"
          :class="{ select: matter_label === item.id }"
          @click="matter_label = item.id"
        >
          {{ item.text }}
        </p>
      </div>
      <!-- 等级 -->
      <div class="addtask-set-item-box">
        <p class="set-title">等级：</p>
        <div class="stars-box">
          <Stars
            class="stars"
            :isCLickActive="true"
            :num="matter_grade"
            :size="20"
            @getStarsnum="getStarsnum"
          ></Stars>
        </div>
      </div>
      <!-- 状态 -->
      <div class="addtask-set-item-box">
        <p class="set-title">状态：</p>
        <span
          v-for="item in stateList"
          class="state iconfont"
          :class="[item.id, { select: matter_state === item.id }]"
          :key="item.id"
          @click="prohibitTips(matter_state === item.id)"
          >{{ item.icon }} {{ item.text }}</span
        >
      </div>
      <!-- 信息输入部分 -->
      <div class="addtask-set-item-box">
        <input
          v-model.trim="matter_title"
          type="text"
          placeholder="任务标题"
          class="todo-input"
          @keyup.enter="confirmEditing"
        />
        <textarea
          v-model.trim="matter_content"
          class="my-note"
          placeholder="任务描述"
        ></textarea>
      </div>
      <div class="addtask-set-item-box">
        <p
          class="add-task-btn"
          :class="{ 'is-change': !isChange }"
          @click="confirmEditing"
        >
          确认修改
        </p>
      </div>
    </div>
  </h-portabilityBox>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance,
  watch
} from 'vue';
import { stateList, typeList } from './config.js';
export default {
  props: {
    editTack: {
      type: Object
    }
  },
  name: '',
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      stateList,
      typeList,
      myEditTack: null, //编辑任务信息
      isChange: false //是否编辑内容改变
    });
    watch(
      // 监听目录
      () => data.myEditTack,
      () => {
        // 获取编辑前和编辑后数据字符串对比，是否改变
        const originalStr = JSON.stringify(props.editTack);
        const editStr = JSON.stringify(data.myEditTack);
        data.isChange = originalStr !== editStr;
      },
      { deep: true }
    );
    onBeforeMount(() => {});
    onMounted(() => {});
    // 获取内部可编辑信息
    const getMyEditTack = () => {
      // 将传递参数只拷贝第一层即可
      const { ...editTack } = props.editTack;
      data.myEditTack = editTack;
    };
    getMyEditTack();
    // 触发父组件关闭弹窗
    const closePortability = () => {
      ctx.emit('closePortability');
    };
    // 触发等级修改
    const getStarsnum = (val) => {
      data.myEditTack.matter_grade = val;
    };
    // 状态禁止编辑提示
    const prohibitTips = (bool) => {
      // 点击选中当前选中状态不提示
      if (bool) return;
      // 点击灰色状态提示不可编辑
      proxy.h_tips({
        type: 'error',
        msg: '状态不可编辑,编辑后可在任务栏切换',
        top: 20,
        time: 2000
      });
    };
    // 确认编辑
    const confirmEditing = () => {
      // 判断无变化就不执行点击编辑
      if (!data.isChange) return;
      // 先判断有没有标题
      if (data.myEditTack.matter_title) {
        // 确认编辑后增加编辑时间
        data.myEditTack.matter_edit_date = new Date().toLocaleString();
        // 触发todoList中的编辑事件，使用mitt事件总线
        proxy.$bus.emit('editTask', data.myEditTack);
        closePortability();
        proxy.h_tips({
          type: 'success',
          msg: ' 编辑成功',
          top: 20,
          time: 2000
        });
      } else {
        proxy.h_tips({
          type: 'info',
          msg: ' 请输入任务标题',
          top: 20,
          time: 2000
        });
      }
    };

    return {
      closePortability,
      confirmEditing,
      getStarsnum,
      prohibitTips,
      ...toRefs(data.myEditTack),
      ...toRefs(data)
    };
  }
};
</script>
