<template>
  <div class="todolist-page">
    <!-- 左边部分收缩按键 -->
    <p
      class="iconfont icon-xiangzuoshouqi"
      :class="{ leftBoxSwitch }"
      @click="leftBoxSwitch = !leftBoxSwitch"
    ></p>
    <!-- 左侧大盒子 -->
    <div class="todolist-box-l" :class="{ leftBoxSwitch }">
      <div class="todolist-add-box">
        <!-- 右上角功能盒子 -->
        <div class="func-box">
          <div class="func-btn">
            <p class="ipt_zzc_b">导入</p>
            <input
              class="downloade_export_ipt"
              title="添加新导入事项，不影响已经存在的事项"
              type="file"
              accept=".txt"
              @change="upfile($event)"
            />
          </div>
          <p class="func-btn out" @click="exportLocal">导出</p>
          <p class="func-btn empty-all" @click="emptyAllTaskList">清空</p>
        </div>
        <!-- 左侧添加任务盒子 -->
        <div class="add-box">
          <addTask @addTask="addTask"></addTask>
        </div>
      </div>
    </div>
    <!-- 右侧大盒子 -->
    <div class="todolist-box-R" :class="{ leftBoxSwitch }">
      <taskList
        :taskListData="taskListData"
        @deletedTaskHome="deletedTaskHome"
        @changeState="changeState"
      ></taskList>
    </div>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  getCurrentInstance
} from 'vue';
import { getLS, setLS, removeLS } from '@/utils/common.js';
import addTask from './addTask.vue';
import taskList from './taskList.vue';
import { STATE } from './config.js';
export default {
  name: '',
  components: {
    addTask,
    taskList
  },
  setup() {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      leftBoxSwitch: false, //控制左侧显示隐藏
      taskListData: {}, //保存本地全部任务数据
      exportLocalTips: '请勿编辑文件，避免导入错误，'
    });

    onBeforeMount(() => {});
    onMounted(() => {
      // 使用mitt监听编辑事件
      proxy.$bus.on('editTask', editTask);
    });
    // 获取全部本地数据
    const getAllTaskData = () => {
      for (let key in STATE) {
        data.taskListData[STATE[key]] = getLS(STATE[key]);
      }
      // console.log(data.taskListData);
    };

    getAllTaskData();
    // 获取增加的任务信息
    const addTask = (task) => {
      // 判断数据属性是否正确
      if (
        task &&
        task.matter_state &&
        task.matter_id &&
        task.matter_date &&
        task.matter_title &&
        task.matter_grade &&
        task.matter_label
      ) {
        const state = task.matter_state; //增加任务的状态
        data.taskListData[state].unshift(task); //增加对应状态的数据
        setLS(state, data.taskListData[state]); //再把对应更新状态的数据全部保存至本地
      } else {
        console.log('数据格式不正确');
      }
    };
    // 子组件触发的删除操作
    const deletedTaskHome = (task) => {
      const state = task.matter_state; //增加任务的状态
      const targetArr = data.taskListData[state]; //要操作的任务数组
      targetArr.forEach((item, index) => {
        //删除指定id任务
        if (item.matter_id === task.matter_id) {
          targetArr.splice(index, 1);
        }
      });
      setLS(state, data.taskListData[state]); //再把对应更新状态的数据全部保存至本地
    };
    // 改变任务状态
    const changeState = (val) => {
      const targetObj = val[0]; //操作对象
      const targetState = val[1]; //目标状态
      deletedTaskHome(targetObj); //先从原来状态数组中删除
      targetObj.matter_state = targetState; //改变操作对象的状态
      addTask(targetObj); //在添加到目标状态数组
    };

    // 清空全部任务列表
    const emptyAllTaskList = () => {
      proxy
        .h_confirm({
          title: '提示',
          content: '确认删除全部任务 ？',
          style: {
            box: {
              top: 300,
              bgColor: '#fff',
              width: 360,
              padding: 10,
              borderRadius: 4
            },
            content: {
              conColor: 'red'
            }
          }
        })
        .then(() => {
          // 循环清除本地任务列表
          for (let key in STATE) {
            removeLS(STATE[key]);
          }
          //清除后从新获取数据
          getAllTaskData();
        });
    };
    // 编辑事件触发
    const editTask = (val) => {
      // 获取编辑的状态数组
      const editList = data.taskListData[val.matter_state];
      // 遍历数组通过matter_id找到编辑元素的下标
      editList.forEach((item, index) => {
        if (item.matter_id === val.matter_id) {
          // 替换当前元素
          editList.splice(index, 1, val);
        }
      });
      setLS(val.matter_state, editList); //再把对应更新状态的数据全部保存至本地
    };

    // 选择txt读取内容
    const upfile = (e) => {
      let resultFile = e.target.files[0]; //获取文件1信息
      // 如果文件存在
      if (resultFile) {
        // 使用 FileReader 来读取文件
        let reader = new FileReader();
        // 读取纯文本文件,且编码格式为 utf-8
        reader.readAsText(resultFile, 'UTF-8');
        // 读取文件,会触发 onload 异步事件,可使用回调函数 来获取最终的值.
        reader.onload = (e) => {
          let fileContent = e.target.result; //文件文本内容
          addTaskFiles(fileContent); //增加新任务
        };
      }
    };
    // 选择文件
    const addTaskFiles = (fileContent) => {
      let idList = []; //保存原有数据所有id
      for (let key in data.taskListData) {
        data.taskListData[key].forEach((task) => {
          idList.push(task.matter_id);
        });
      }

      //分割换行 转为数组
      const arr = fileContent.split(/[(\r\n)\r\n]+/);
      console.log(arr)
      if (!arr[0].includes(data.exportLocalTips)) {
        console.log('文件格式不正确');
        return;
      }
      if (arr.length === 4) {
        arr.shift(); //删除数组第一个日期
        arr.forEach((item) => {
          let taskItem = JSON.parse(item);
          // 数组有长度就追加
          if (taskItem.length) {
            // 循环数组判断是否存在的id
            taskItem.forEach((append) => {
              if (idList.length) {
                // 源数据id中是否包含要追加的id
                if (idList.includes(append.matter_id)) return;
                // 增加任务
                addTask(append);
              } else {
                // 没有原始数据就直接追加
                addTask(append);
              }
            });
          }
        });
      } else {
        console.log('文件格式不正确');
      }
    };

    // 导出全部任务
    const exportLocal = () => {
      // 导出文件内部title标识
      const title =
        data.exportLocalTips +
        '导出TodoList时间：' +
        new Date().toLocaleString();
      let allStr = title;
      // 循环拼接所有任务列表数据
      for (let key in STATE) {
        allStr += '\r\n' + JSON.stringify(getLS(STATE[key]));
      }
      //下载内容，标题加本地数据字符串
      const export_blob = new Blob([allStr]);
      //创建a标签点击可下载
      const save_link = document.createElement('a');
      save_link.href = window.URL.createObjectURL(export_blob);
      //下载时文件名
      save_link.download = 'TodoList.txt';
      fakeClick(save_link);
    };

    // txt文件导出
    const fakeClick = (obj) => {
      const ev = document.createEvent('MouseEvents');
      ev.initMouseEvent(
        'click',
        true,
        false,
        window,
        0,
        0,
        0,
        0,
        0,
        false,
        false,
        false,
        false,
        0,
        null
      );
      obj.dispatchEvent(ev);
    };
    return {
      upfile,
      exportLocal,
      editTask,
      addTask,
      getAllTaskData,
      deletedTaskHome,
      changeState,
      emptyAllTaskList,
      ...toRefs(data)
    };
  }
};
</script>
