<template>
  <div class="list-item-box-inside">
    <!-- 状态盒子 -->
    <div class="list-item-state">
      <!-- 性质 -->
      <div class="list-item-type" :class="taskData.matter_label">
        {{ TASK_TYPE[taskData.matter_label] }}
      </div>
      <!-- 等级盒子 -->
      <div class="list-stars-box">
        <Stars
          class="stars"
          :color="'rgba(0,0,0,0)'"
          :readOnly="true"
          :num="taskData.matter_grade"
          :size="14"
        ></Stars>
      </div>
      <!-- 删除编辑 -->
      <div class="list-item-state-r">
        <p
          class="iconfont icon-youliaobiji edit"
          title="编辑任务"
          @click="(openEditBox = true), (editTack = taskData)"
        ></p>
        <p
          class="iconfont icon-shanchu deleted"
          @click="deletedTask(taskData)"
          title="删除任务"
        ></p>
      </div>
      <!-- 状态盒子 -->
      <div class="list-icon-box">
        <!-- 高亮对应状态 -->
        <p
          v-for="item in stateList"
          class="iconfont state"
          :title="item.text"
          :class="[item.id, { select: taskData.matter_state === item.id }]"
          :key="item"
          @click="changeState(taskData, item.id)"
        >
          {{ item.icon }}
        </p>
      </div>
    </div>
    <div class="list-item-title">
      <div class="time-box">
        <p class="time-text">创建：{{ taskData.matter_date }}</p>
        <p class="time-text edit" v-if="taskData.matter_edit_date">
          更新：{{ taskData.matter_edit_date }}
        </p>
        <!-- 展开收起 -->
        <p
          class="iconfont icon-xiangzuoshouqi open"
          :class="{ myIsOpen }"
          @click="myIsOpenChange"
        ></p>
      </div>
      <div class="list-item-title-text">{{ taskData.matter_title }}</div>
      <!-- 展开内容 -->
      <h-transitionH>
        <div class="open-content" v-if="myIsOpen">
          <!-- pre保留文本格式 -->
          <pre class="task-content" v-if="taskData.matter_content">{{
            taskData.matter_content
          }}</pre>

          <p v-else class="no-content">无任务描述</p>
        </div>
      </h-transitionH>
    </div>
    <task-edit
      v-if="openEditBox"
      :editTack="editTack"
      @closePortability="closePortability"
    ></task-edit>
  </div>
</template>

<script>
import {
  reactive,
  toRefs,
  onBeforeMount,
  onMounted,
  watch,
  getCurrentInstance
} from 'vue';
import { TASK_TYPE, stateList } from './config.js';
import taskEdit from './taskEdit.vue';
export default {
  props: {
    taskData: {
      //单个任务数据
      type: Object
    },
    openAll: {
      //全部展开状态
      type: Boolean,
      default: false
    },
    // 重置单个关闭，在改变的列表使用
    resetOneOpen: {
      type: Boolean
    }
  },
  name: '',
  components: { taskEdit },
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      TASK_TYPE,
      stateList,
      myIsOpen: false,
      openEditBox: false, //打开编辑盒子
      editTack: null //编辑对象
    });
    watch(
      // 请求返回的数据
      () => props.openAll,
      (val) => {
        data.myIsOpen = val;
      },
      { immediate: true }
    );
    watch(
      // 请求返回的数据
      () => props.resetOneOpen,
      () => {
        data.myIsOpen = false;
      }
    );
    onBeforeMount(() => {});
    onMounted(() => {});
    // 关闭任务编辑弹窗
    const closePortability = () => {
      data.openEditBox = false;
    };
    // 触发父组件删除
    const deletedTask = (task) => {
      // 再次提示框
      proxy
        .h_confirm({
          title: '提示',
          content: '确认删除该任务 ？',
          style: {
            box: {
              top: 300,
              bgColor: '#fff',
              width: 360,
              padding: 10,
              borderRadius: 4
            },
            content: {
              conColor: 'red'
            }
          }
        })
        .then(() => {
          // 触发删除
          ctx.emit('deletedTaskList', task);
        })
        .catch(() => {
          console.log('取消');
        });
    };
    // 改变状态  当前任务对象，要改变的状态
    const changeState = (current, target) => {
      if (current.matter_state === target) return;
      ctx.emit('changeState', [current, target]);
      // console.log(current, target);
    };
    // 改变单个展开状态
    const myIsOpenChange = () => {
      data.myIsOpen = !data.myIsOpen;
      ctx.emit('myIsOpenChange', [props.taskData.matter_state, data.myIsOpen]);
    };
    return {
      changeState,
      myIsOpenChange,
      deletedTask,
      closePortability,
      ...toRefs(data)
    };
  }
};
</script>
